// app/api/rms/station-info/route.ts

import { NextRequest, NextResponse } from 'next/server';
import dbConnect from '@/lib/dbConnect'; // ✅ default import
import { Station } from '@/models/station'; // ✅ named import

// ── GET: fetch all stations
export async function GET() {
  try {
    await dbConnect();
    const stations = await Station.find().sort({ createdAt: -1 });
    return NextResponse.json(stations);
  } catch (err) {
    return NextResponse.json({ message: 'Fetch failed' }, { status: 500 });
  }
}

// ── POST: create new station
export async function POST(req: NextRequest) {
  try {
    await dbConnect();
    const body = await req.json(); // ← JSON body from Postman or client
    const station = await Station.create(body);
    return NextResponse.json(station, { status: 201 });
  } catch (err: any) {
    return NextResponse.json({ message: err.message }, { status: 400 });
  }
}
