// models/station.ts

import mongoose, { Schema, model, models } from 'mongoose';

const StationSchema = new Schema({
  StationCode: { type: String, required: true, unique: true },
  RegionalLanguage: { type: String, required: true },
  StationNameEnglish: { type: String, required: true },
  StationNameHindi: { type: String, required: true },
  StationNameRegional: { type: String, required: true },
  Latitude: Number,
  Longitude: Number,
  Altitude: Number,
  NumberOfPlatforms: Number,
  NumberOfSplPlatforms: Number,
  NumberOfStationEntrances: Number,
  NumberOfPlatformBridges: Number,
}, {
  timestamps: true,
});

export const Station = models.Station || model("Station", StationSchema);
